import React, { useState } from "react";
import Project from "./Project";
import ProjectCarousel from "./ProjectCarousel";

import imgMarkdownPreviewer from "../assets/img/projects/markdown_previewer.jpg";
import imgTechnicalDocumentation from "../assets/img/projects/technical_documentation_page.jpg";
import imgHydivHomepage from "../assets/img/projects/hydiv_homepage.jpg";
import imgImpostoreWebstore from "../assets/img/projects/impostore_webstore.jpg";
import imgLoopstudiosHomepage from "../assets/img/projects/loopstudios_homepage.jpg";

const Projects = () => {
  const project = [
    {
      id: 1,
      name: "Loopstudios Landing Page",
      description:
        "Mobile responsive and interactive landing page made with React and SCSS.",
      link: "https://loopstudios-webpage.netlify.app/",
      image: imgLoopstudiosHomepage,
      alt: "loopstudios landing page",
    },
    {
      id: 2,
      name: "Impostore Webstore",
      description:
        "A front-end online store that displays product information and categories from a REST API using Next.js and CSS modules",
      link: "https://impostore-webstore.netlify.app/",
      image: imgImpostoreWebstore,
      alt: "impostore webstore",
    },
    {
      id: 3,
      name: "HYDIV Landing Page",
      description:
        "A simple landing page of a furniture store made using React-based component library ChakraUI",
      link: "https://hydiv-homepage.netlify.app/",
      image: imgHydivHomepage,
      alt: "hydiv landing page",
    },
    {
      id: 4,
      name: "Markdown Previewer",
      description:
        "Formats plain text into a more appealing and user-friendly reading experience using Markdown.",
      link: "https://codepen.io/derianmelvin/pen/abmxZmY",
      image: imgMarkdownPreviewer,
      alt: "markdown previewer",
    },
    {
      id: 5,
      name: "Technical Documentation",
      description:
        "View and navigate between sections of documentation details using pure HTML and CSS.",
      link: "https://codepen.io/derianmelvin/pen/LYZoMVj",
      image: imgTechnicalDocumentation,
      alt: "technical documentation",
    },
  ];
  const [currentProject, setCurrentProject] = useState(0);

  const handleProject = (index) => {
    setCurrentProject(index);
  };

  return (
    <main id="projects">
      <div id="projects-header-container">
        <div id="projects-nav"></div>
        <h2 id="projects-title">Projects</h2>
      </div>

      <div id="projects-content-container">
        <div id="projects-project-info">
          <Project
            project={project[currentProject]}
          />
        </div>

        <div id="projects-project-image">
          <ProjectCarousel
            projects={project}
            currentProject={currentProject}
            handleProject={handleProject}
          />
        </div>
      </div>
    </main>
  );
};

export default Projects;
