const Project = ({ project }) => {
  return (
    <>
      <h3 id="project-name">
        <em>{project.name}</em>
      </h3>

      <div id="project-content">
        <p id="project-description">{project.description}</p>
      </div>

      <a
        id="project-button"
        href={project.link}
        target="_blank"
        rel="noopener noreferrer"
      >
        See Project
      </a>
    </>
  );
};

export default Project;
