import Carousel from "react-bootstrap/Carousel";

const ProjectCarousel = ({ projects, currentProject, handleProject }) => {
  return (
    <Carousel activeIndex={currentProject} onSelect={handleProject} pause="hover">
      <Carousel.Item>
        <img
          src={projects[0].image}
          alt={projects[0].alt}
        />
      </Carousel.Item>
      <Carousel.Item>
        <img
          src={projects[1].image}
          alt={projects[1].alt}
        />
      </Carousel.Item>
      <Carousel.Item>
        <img
          src={projects[2].image}
          alt={projects[2].alt}
        />
      </Carousel.Item>
      <Carousel.Item>
        <img
          src={projects[3].image}
          alt={projects[3].alt}
        />
      </Carousel.Item>
      <Carousel.Item>
        <img
          src={projects[4].image}
          alt={projects[4].alt}
        />
      </Carousel.Item>
    </Carousel>
  );
};

export default ProjectCarousel;
